<template>
  <div id="app" class="app-viewport">
    <v-tabs icons-and-text centered color="blue-grey darken-1">
      <v-tabs-slider color="white" style="width:100%;"></v-tabs-slider>
      <v-tab to="/">
        Matchs
        <v-icon color="white">access_time</v-icon>
      </v-tab>
      <v-tab to="/classements" style="text-decoration:none;">
        Classement
        <v-icon color="white" >start</v-icon>
      </v-tab>
    </v-tabs>

    <div class="md-layout" style="background-color:#FFF59D" id="connexionBar" ref="connexionBar" v-show="connexionDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Vous êtes hors connexion ...</div>
    </div>

    <div class="md-layout" style="background-color:#FFF59D" id="serverBar" ref="serverBar" v-show="serverDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Serveur injoignable ...</div>
    </div>
    <div class="simple">
      10h32
    </div>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
let connexionDown = false
let serverDown = false
var debounce = require('lodash.debounce')

export default {
  name: 'app',

  methods: {
    showConnexionBar (event) {
      this.$localStorage.set('online', false)
      this.connexionDown = true
      window.scrollTo(0, 0)
    },
    hideConnexionBar (event) {
      this.$localStorage.set('online', true)
      this.connexionDown = false
    },
    showServerBar () {
      this.serverDown = true
      window.scrollTo(0, 0)
    },
    hideServerBar () {
      this.serverDown = false
    },
    updateClassement () {
      this.$http.get(process.env.CLASSEMENT_JSON).then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.$store.commit('setClassements', response.data)
        this.$localStorage.set('classements', JSON.stringify(response.data))
      }, response => {
        // error callback
        if (this.$localStorage.get('classements') !== null) {
          this.$store.commit('setClassements', JSON.parse(this.$localStorage.get('classements')))
        }
        console.log('error call data')
      })
    },
    updateMatche () {
      this.$http.get(process.env.MATCHS_JSON).then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.$store.commit('setMatches', response.data)
        this.$localStorage.set('matches', JSON.stringify(response.data))
        this.notifications = response.data.notifications
      }, response => {
        // error callback
        if (this.$localStorage.get('matches') !== null) {
          this.$store.commit('setMatches', JSON.parse(this.$localStorage.get('matches')))
        }
        console.log('error call data')
      })
    },
    updateClassements: debounce(function () {
      this.updateClassement()
    }, 10000),
    updateMatches: debounce(function () {
      this.updateMatche()
    }, 10000),
    checkNotif: function () {
      for (var key in this.notifications) {
        var item = this.notifications[key]
        if (item.id > this.$localStorage.get('lastPushId')) {
          // console.log('show push:' + item.id)
          this.showPush(item)
        }
      }
    },
    showPush (item) {
      // console.log(item.type)
      Notification.requestPermission(function (result) {
        if (result === 'granted') {
          this.$localStorage.set('lastPushId', item.id)
          if (item.type === 'BUT') {
            this.showBut({title: item.title, message: item.message})
          }
          if (item.type === 'JAUNE') {
            this.showCartonJaune({title: item.title, message: item.message})
          }
          if (item.type === 'ROUGE') {
            this.showCartonRouge({title: item.title, message: item.message})
          }
          if (item.type === 'INFO') {
            this.showInfo({title: item.title, message: item.message})
          }
        }
      })
    }
  },
  data () {
    return {
      connexionDown,
      serverDown,
      notifications: '',
      notification: ''
    }
  },
  created: function () {
    this.updateMatche()
    this.updateClassement()
  },

  mounted () {
    console.log('app.vue mounted')
    window.addEventListener('online', this.hideConnexionBar)
    window.addEventListener('offline', this.showConnexionBar)
    if (!navigator.onLine) this.showConnexionBar()
  },
  watch: {
    notif: function () {
      self.addEventListener('notificationclick', event => {
        const notification = event.notification
        const action = event.action
        const link = notification.data.link
        if (action !== 'close') {
          if (link) {
            this.openWindow(link)
          }
        }
        notification.close()
        console.log('notificationclick action is', action)
      })
    },
    classements: function () {
      this.updateClassements()
    },
    matches: function () {
      this.updateMatches()
    }
  },

  notifications: {
    showBut: {
      type: 'but',
      timeout: 3000
    },
    showCartonJaune: {
      type: 'warn',
      timeout: 2000
    },
    showCartonRouge: {
      type: 'pen',
      timeout: 3000
    },
    showInfo: {
      type: 'info',
      timeout: 2000
    }
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  margin-top: 0px;
}

.complete-example {
  height: 540px;
  display: flex;
  flex-flow: column;
  position: relative;
  overflow: hidden;
  z-index: 1;

  .main {
    overflow: auto;
  }
}

h2 {
  padding-left: 50px;
}

md-toolbar>md-tabs{
  height: auto;
  width: auto;
}
.md-tabs + .md-tabs {
  margin-top: 24px;
}
a:not(.md-button):hover {
  text-decoration: none !important;
}
</style>



